<template>
  <router-view></router-view>

  <SubFolderHandler ref="subFolderHandler"/>
</template>
<script>
import { mapState } from 'vuex'
import tools from '@/utils/tools'

import SubFolderHandler from '@/components/content/SubFolderHandler'
tools.getWindowArgs(window)
import configDb from '@/utils/db/configDb'
import libDb from '@/utils/db/libDb'
configDb.init(window.globalArgs['user-data-dir'])//用用户存档路径来初始化configDB，从而获取storePath
window.configDb=configDb//挂载到窗体全局供调用
const defaultStorePath =  configDb.getStorePath()+'/' //window.globalArgs['user-data-dir'] + '/收藏夹.lab'+'/'
window.$defaultStrorePath=defaultStorePath
libDb.init(defaultStorePath) //读入libDB
window.libDb=libDb //挂载到全局供使用
export default {
  name: 'App',
  components: {
    SubFolderHandler,
    // HelloWorld,
  },
  data(){
    return{
      isApp:window.isApp
    }
  },
  computed:{
    ...mapState(
      {
        userInfo:state=>state.user.userInfo,
        storePath:state=>state.config.storePath
      }
      )
  },
  mounted () {
    document.onkeydown=(e)=>{
      if(e.keyCode==32){
       this.$store.commit('ui/setPreview',true)
      }
    }
    document.onkeyup=(e)=>{
      if(e.keyCode==32){
        this.$store.commit('ui/setPreview',false)
      }
    }
    //初始化，设置默认的StorePath，这里还需要//todo 读入用户设置的本地路径
    this.$store.commit('config/setStorePath',defaultStorePath)
  },
  methods:{

  }
}
</script>

