
const fileHelper = {
    ignore:['.DS_Store', '_assets', '_db.json','_index.json'],
    /**
     * 返回目录
     * @param dir
     * @param options
     * @returns {{tree: *[]}}
     */
    getFileTree(dir, options) {
        const libPath=window.configDb.getStorePath()
         options=Object.assign( {
             showFile: true,//显示文件
             gotFlat: true,//返回扁平结构
             filters: fileHelper.ignore,
             mapCallback:null,//包含文件或者文件名
             relative:false//包含相对路径
         },options)
        let path = eval('require')('path');
        let fs = eval('require')('fs');
        var tree = []
        let flat = []//扁平结构
        //let cur = 0
        // 用个hash队列保存每个目录的深度
        var mapDeep = {}
        mapDeep[dir] = 0
        // 先遍历一遍给其建立深度索引
        // function getMap(dir, curIndex) {
        //     var files = fs.readdirSync(dir) //同步拿到文件目录下的所有文件名
        //     files.map(function (file) {
        //         //var subPath = path.resolve(dir, file) //拼接为绝对路径
        //         var subPath = path.join(dir, file) //拼接为相对路径
        //         var stats = fs.statSync(subPath) //拿到文件信息对象
        //         // 必须过滤掉filters里的文件和文件夹
        //         if (options.filters.indexOf(file)===-1) {
        //             mapDeep[file] = curIndex + 1
        //             if (stats.isDirectory()) { //判断是否为文件夹类型
        //                 return getMap(subPath, mapDeep[file]) //递归读取文件夹
        //             }
        //         }
        //     })
        // }
        // if(options.showDeep){
        //     getMap(dir, mapDeep[dir])
        // }

        function readDirs(dir) {
            var result = { //构造文件夹数据
                path: dir,
                title: path.basename(dir),
                type: 'dir',
            }
            var files = fs.readdirSync(dir) //同步拿到文件目录下的所有文件名
            result.children = files.map(function (file) {
                if(options.filters.indexOf(file)>-1) return
                if(options.mapCallback){
                    options.mapCallback(dir,file)
                }
                //var subPath = path.resolve(dir, file) //拼接为绝对路径
                var subPath = path.join(dir, file) //拼接为相对路径
                var stats = fs.statSync(subPath) //拿到文件信息对象
                if (stats.isDirectory() && !file.endsWith('.pxd')) { //判断是否为文件夹类型
                    //子文件排除了.pxd的子文件
                    return readDirs(subPath, file, file) //递归读取文件夹
                }
                let fileInfo={ //构造文件数据
                    path: subPath,
                    name: file,
                    type: 'file',
                }
                if(options.relative){
                    fileInfo.relativePath=subPath.replace(libPath,'')
                }
                if(options.gotFlat){
                    flat.push(fileInfo)
                    if(options.showFile){//如果不显示文件，则不进行返回
                        return fileInfo
                    }else{
                        return false
                    }
                }
            })
            let flatObject={
                path:result.path,
                name:result.title,
                type:result.type
            }
            if(options.relative){
                flatObject.relativePath=result.path.replace(libPath,'')
            }
            flat.push(flatObject)
            return result //返回数据
        }

        tree.push(readDirs(dir, dir))
        let result = {
            tree: tree
        }
        if (options.gotFlat) {
            result['flat'] = flat
        }
        return result
    }
}

export default fileHelper
