<template>
  <div style="position: relative">
    <h3 style="margin-bottom:10px;margin-top: 20px">内容（{{ contentsOrderByConfig.length }}）</h3>
    <div style="position: fixed;display: inline-block;width: 30px;height: 30px;text-align: center">

    </div>

    <div class="context-menu" style="" v-show="menuVisible" :style="menuStyle">
      <a-menu >
        <a-menu-item key="openPath" @click="open(content)">打开</a-menu-item>
        <a-menu-item key="find" @click="showItemInFolder(content.baseInfo.path+'/'+content.baseInfo.filename)">
          查看所在位置
        </a-menu-item>
        <a-menu-item key="delete" @click="trashItem(content)">删除文件
        </a-menu-item>
        <a-menu-item v-if="content.type==='img'" key="edit"
                     @click="editPicture(content.baseInfo.path+'/'+content.baseInfo.filename)">编辑图片
        </a-menu-item>
        <a-menu-item v-if="content.type==='img'" key="setWallpaper"
                     @click="setNewTabWallPaper(content.baseInfo.path+'/'+content.baseInfo.filename)">设为新标签页默认壁纸
        </a-menu-item>
        <a-menu-item v-if="content.type==='img'" key="setDesktopWallPaper"
                     @click="setDeskWallpaper(content.baseInfo.path+'/'+content.baseInfo.filename)">设置为桌面壁纸
        </a-menu-item>
      </a-menu>
    </div>
    <a-table style="position:relative" :pagination="false" :customRow="handleRow" size="small" :columns="columns" :data-source="contentsOrderByConfig">

      <template #headerCell="{ column }">
        <template v-if="column.key === 'name'">
        <span>
          名称
        </span>
        </template>
      </template>

      <template #bodyCell="{ column, record }">
        <template v-if="column.key === 'name'">
          <span>
            <a-avatar shape="square" :src="getCover(record)"/>
            {{ record.name }}
          </span>
        </template>
        <template v-if="column.key === 'url'">
          <span>
            {{ record.meta.href }}
          </span>
        </template>
        <template v-if="column.key==='type'">
          <span>
            {{ typeAlias(record)}}
          </span>
        </template>
        <template v-else-if="column.key === 'size'">
        <span>
          {{ convertToText(record.baseInfo.size) }}
        </span>
        </template>
        <template v-else-if="column.key === 'action'">
        <span>
          <a>删除</a>
        </span>
        </template>
      </template>
    </a-table>

    <div  id="itemsLists" style="min-height:150px;display: none">
      <a-empty style="margin-top: 60px" description="" v-if="contentsOrderByConfig.length===0"></a-empty>
      <a-dropdown v-else :trigger="['contextmenu']" v-for="content in contentsOrderByConfig" v-bind:key="content">
        <div class="content-wrapper" @mousedown.stop="()=>{}" @mouseup="dragging=false" :uuid="content.meta.uuid"
             draggable="true"
             @dragstart="onDragStart($event,content)"
             @dragend="onDragEnd"
             @dragover="onDragOver"
             :class="{'selected':!!this.focusItem.data && this.focusItem.data.meta.uuid===content.meta.uuid}"
             @click="selectedKey=content"
             :style="{width:itemSize+'px',height:itemSize+50+'px'}">
          <template v-if="content.extCanOpenInside">
            <TypePwd @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='pwd'"
                     v-bind:content="content"/>
            <TypeImg @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='img'"
                     v-bind:content="content"/>
            <TypeUrlList @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='url'"
                         v-bind:content="content"/>
            <TypeVideo @setSelected="selectItem(content)" @openDetail="openDetail" v-if="content.type==='video'"
                       v-bind:content="content"/>
          </template>
          <template v-else>
            <TypeOther @setSelected="selectItem(content)" @openDetail="openPath" v-bind:content="content"/>
          </template>
          <div v-if="content.meta.new" class="type-label new-label">New</div>
        </div>
        <template #overlay>
          <a-menu>
            <a-menu-item key="openPath" @click="open(content)">打开</a-menu-item>
            <a-menu-item key="find" @click="showItemInFolder(content.baseInfo.path+'/'+content.baseInfo.filename)">
              查看所在位置
            </a-menu-item>
            <a-menu-item key="delete" @click="trashItem(content)">删除文件
            </a-menu-item>
            <a-menu-item v-if="content.type==='img'" key="edit"
                         @click="editPicture(content.baseInfo.path+'/'+content.baseInfo.filename)">编辑图片
            </a-menu-item>
            <a-menu-item v-if="content.type==='img'" key="setWallpaper"
                         @click="setNewTabWallPaper(content.baseInfo.path+'/'+content.baseInfo.filename)">设为新标签页默认壁纸
            </a-menu-item>
            <a-menu-item v-if="content.type==='img'" key="setDesktopWallPaper"
                         @click="setDeskWallpaper(content.baseInfo.path+'/'+content.baseInfo.filename)">设置为桌面壁纸
            </a-menu-item>
          </a-menu>
        </template>
      </a-dropdown>
    </div>
  </div>
  <VueSelecto
      dragContainer=".ant-layout-content"
      :selectableTargets='[".content-wrapper"]'
      :selectByClick="true"
      :selectFromInside="true"
      :continueSelect="false"
      :toggleContinueSelect='"shift"'
      :hitRate="0"
      @select="onSelect"
  />
</template>
<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import TypePwd from '@/components/content/type/TypePwd'
import TypeImg from '@/components/content/type/TypeImg'
import { Modal } from 'ant-design-vue'
import ipc from '@/utils/ipc'
import TypeOther from '@/components/content/type/TypeOther'
import TypeVideo from '@/components/content/type/TypeVideo'
import TypeUrlList from '@/components/content/typeList/TypeUrlList'
import { VueSelecto } from 'vue3-selecto'
import contentModel from '@/utils/model/contentModel'
import pathTools from '@/utils/pathTools'
import tools from '@/utils/tools'
import contentTypeHelper from '@/utils/contentTypeHelper'

const columnsUrl = [
  {
    name: '名称',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '网址',
    dataIndex: 'url',
    key: 'url',
  }
]
const columns = [
  {
    name: '名称',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '类型',
    dataIndex: 'type',
    key: 'type',
  },
  {
    title: '大小',
    dataIndex: 'size',
    key: 'size',
  },

]
const wallpaper = eval('require')('wallpaper')
export default {
  name: 'ContentItemListList',
  components: { TypePwd, TypeImg, TypeOther, TypeVideo, TypeUrlList, VueSelecto },
  props: {},
  data () {
    return {
      selectedKey: null,
      dragging: false,
      left: 0,
      top: 0,
      timer: null,
      content:{},
      menuStyle:{},
      menuVisible:false,
    }
  },
  mounted () {
  },

  methods: {
    convertToText(size){
      return tools.convertToText(size)
    },
    cancelClick () {
      this.menuVisible = false
      document.body.removeEventListener("click", this.cancelClick)
    },
    handleRow (record) {
      return {
        onClick: () => {
          this.selectItem(record)
        },
        onDblclick:()=>{
          this.openDetail(record)
        },
        onContextmenu:(e)=>{
          e.preventDefault()
          this.content=record
          this.menuVisible=true
          this.menuStyle.top=e.clientY+"px"
          this.menuStyle.left=e.clientX+"px"
          document.body.addEventListener("click",this.cancelClick)
        }

      }
    },
    typeAlias (data) {
      try {
        return contentTypeHelper.getContentTypeAliasFromExt(data.ext)
      } catch (e) {
        return '未识别'
      }
    },
    getCover (data) {

      if (data.type === 'url') {
        return data.meta.ext_data.favicon || './img/default.svg'
      } else {
        return contentTypeHelper.getContentCover(data.ext)
      }
    },
    setDeskWallpaper (str) {
      this.$message.success('设置为桌面壁纸成功。')
      wallpaper.set(str)
    },
    onDragOver () {
    },
    async onDragStart (event, content) {
      if (this.$store.state.ui.selectedItems.length <= 1) {
        this.selectItem(content)
        return
      }
      this.dragging = true
      let pic = document.getElementById('selectedItemsCover')
      event.dataTransfer.setDragImage(document.getElementById('selectedItemsCover'), pic.offsetWidth / 2, pic.offsetHeight / 2)
    },
    onDragEnd () {
      this.dragging = false
    },
    onSelect (e) {
      // if(e.selected.length===0){
      //   return
      // }
      // let all=document.getElementsByClassName('selected')
      // for(let i=0;i<all.length;i++){
      //   all[i].classList.remove('selected')
      // }
      // this.$store.commit('ui/clearFocusItem')
      e.added.forEach(el => {
        el.classList.add('selected')
      })
      e.removed.forEach(el => {
        el.classList.remove('selected')
      })
      if (e.selected.length === 1) {
        this.$store.commit('ui/focusItem', {
          type: 'content',
          data: contentModel.getContentFromUuid(e.selected[0].attributes['uuid'].value)
        })
        return
      }

      let selectedUuids = []
      e.selected.forEach(sel => {
        selectedUuids.push(sel.attributes['uuid'].value)
      })

      this.$store.commit('ui/setSelectedItemsByUuids', selectedUuids)
    },
    ...mapMutations({
      'unshiftRecent': 'content/unshiftRecent'
    }),
    open (content) {
      if (content.type === 'url') {
        ipc.addTab(content.meta.href)
      } else {
        ipc.openExternal(pathTools.join(content.baseInfo.path, content.baseInfo.filename))
      }
      this.unshiftRecent(content.meta.uuid)
    },
    showItemInFolder: ipc.showItemInFolder,
    selectItem (content) {
      this.$store.commit('ui/focusItem', {
        type: 'content',
        data: content
      })
      this.$store.commit('ui/setSelectedItemsByUuids', [content.meta.uuid])
    },
    openPath (content) {
      if (content.type === 'url') {
        ipc.addTab(content.meta.href)
      } else if (content.ext === '.pdf') {
        let base = 'file://' + window.globalArgs['app-path'] + '/pages/pdfViewer/index.html'
        let queryString = '?url=' + encodeURI(content.baseInfo.path + '/' + content.baseInfo.filename)
        ipc.addTab(base + queryString)
      } else {
        ipc.openExternal(content.baseInfo.path + '/' + content.baseInfo.filename)
      }
      this.unshiftRecent(content.meta.uuid)
    },
    openDetail (content) {
      if (content.type === 'img') {
        this.$router.push({ path: '/main/img/' + encodeURIComponent(content.baseInfo.path + '/' + content.baseInfo.filename) })
      } else if (content.type === 'video') {
        this.$router.push({ path: '/main/video/' + encodeURIComponent(content.baseInfo.path + '/' + content.baseInfo.filename) })
      } else if (content.type === 'url') {
        ipc.addTab(content.meta.href)
      } else {
        ipc.openExternal(pathTools.join(content.baseInfo.path, content.baseInfo.filename))
        return
      }
      this.unshiftRecent(content.meta.uuid)
    },
    trashItem (content) {
      let path = pathTools.join(content.baseInfo.path, content.baseInfo.filename)
      Modal.confirm({
        title: '删除确认',
        content: '是否确认删除内容？此操作会删除内容的其他信息，此操作无法恢复。',
        okText: '确认',
        cancelText: '取消',
        onOk: () => {
          contentModel.removeContent(path)
          window.app.$store.dispatch('subFolder/updateTreeData')
          window.app.$store.dispatch('ui/reload')
          this.$message.success('删除内容成功。')
        }
      })
    },
    //设置新标签页壁纸
    setNewTabWallPaper (wallPaper) {
      ipc.setWallPaper(wallPaper)
      this.$message.success('设置为新标签页桌面默认壁纸成功，如果当前桌面单独设置了壁纸，则仍然以原壁纸显示。未设置单独壁纸的桌面将以新壁纸显示。')
    },
    //通过ipc打开图片编辑器
    editPicture (src) {
      ipc.handleFileAssign('image', { filePath: 'file://' + src })
    }
  },
  computed: {
    columns () {
      if (this.$store.state.ui.currentTab.name === 'bookmark') {
        return columnsUrl
      } else {
        return columns
      }
    },
    ...mapState(
        {
          subFolders: state => state.subFolder.subFolders,
          itemSize: state => state.ui.itemSize,
          contents: state => state.content.contents,
          focusItem: state => state.ui.focusedItem,
          selectedItems: state => state.ui.selectedItems
        }
    ),
    ...mapGetters('content',
        ['contentsOrderByConfig']
    )
  }
}
</script>

<style   >
.context-menu {
  padding:4px 0;
  z-index: 999;
  position: fixed; box-shadow: 0 0 15px rgba(0,0,0,0.2);
  border-radius: 3px;overflow: hidden;
  width:110px !important;
}
.context-menu .ant-menu-item:hover{
  background: #f1f1f1;
  color: #333;
}
.context-menu .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected:hover{
  background: #f1f1f1;
  color: #333;
}
.context-menu .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected{
  background: none;
  color: #333;
}
.context-menu .ant-menu-item{
    line-height: 34px;
  height: 34px;
  padding: 0 12px;
  margin-bottom: 0;
  margin-top: 0;
  }
.context-menu .ant-menu-vertical .ant-menu-item:not(:last-child), .ant-menu-vertical-left .ant-menu-item:not(:last-child), .ant-menu-vertical-right .ant-menu-item:not(:last-child), .ant-menu-inline .ant-menu-item:not(:last-child){
  margin-bottom: 0;
}

</style>
