<template>
  <div>
    <div style="margin-top:20px;position: relative">
      <a-row :gutter="20" type="flex">
        <a-col>
          <a-button v-if="openHistory.length<=1?false:null" @click="()=>{this.$store.dispatch('ui/goBackFolder') }">
            <left-outlined />
          </a-button>
        </a-col>
        <a-col>
          <a-tooltip  v-if="isApp && currentTab.path" placement="bottom" :overlayStyle="{maxWidth:'1000px'}">
            <template #title>
              <span >点击打开完整路径：{{ currentTab.path }} </span>
            </template>
            <span @click="openDir(currentTab.path)">{{currentTab.alias}} <login-outlined /></span>
          </a-tooltip>
          <span v-else>
          {{currentTab.alias}}
        </span>
          {{currentFolder.name}}

        </a-col>
        <a-col  flex="1" style="text-align: right">
            <a-radio-group  v-model:value="layoutType" size="small" style="margin-top: 8px">
              <a-radio-button value="grid"><appstore-outlined /> </a-radio-button>
              <a-radio-button value="list"><unordered-list-outlined /></a-radio-button>
            </a-radio-group>
          &nbsp;
        </a-col>
        <a-col flex="100px">
          <a-slider v-model:value="itemSize" :min="90" :step="5" :max="300" />
        </a-col>
        <a-col flex="120px" >
                         <!--          <a-dropdown>-->
              <!--            <template #overlay>-->
              <!--              <a-menu>-->
              <!--                <a-sub-menu key="info" title="显示信息">-->
              <!--                  <a-menu-item> <a-checkbox  >名称</a-checkbox></a-menu-item>-->
              <!--                  <a-menu-item><a-checkbox  >扩展名</a-checkbox></a-menu-item>-->
              <!--                  <a-menu-item><a-checkbox  >显示子文件夹内容</a-checkbox></a-menu-item>-->
              <!--                </a-sub-menu>-->
              <!--                <a-sub-menu key="order" title="排序方式">-->
              <!--                  <a-menu-item>添加时间</a-menu-item>-->
              <!--                  <a-menu-item>创建时间</a-menu-item>-->
              <!--                  <a-menu-item>修改时间</a-menu-item>-->
              <!--                </a-sub-menu>-->
              <!--                <a-menu-divider></a-menu-divider>-->
              <!--                <a-menu-item key="grid">-->
              <!--                  网格-->
              <!--                </a-menu-item>-->
              <!--                <a-menu-item key="list">-->
              <!--                  列表-->
              <!--                </a-menu-item>-->
              <!--                <a-menu-divider></a-menu-divider>-->
              <!--              </a-menu>-->

              <!--            </template>-->
              <!--            <span  shape="circle" style="margin-right: 10px">-->
              <!--              <BorderOuterOutlined />-->
              <!--            </span>-->

              <!--          </a-dropdown>-->
              <!--          <a-select-->
              <!--              v-model:value="value"-->
              <!--              show-search-->
              <!--              placeholder="搜索"-->
              <!--              style="width: 200px"-->
              <!--              :options="searchHistory"-->
              <!--              :filter-option="filterSearchHistory"-->
              <!--              @focus="handleFocus"-->
              <!--              @blur="handleBlur"-->
              <!--              @change="handleChange"-->
              <!--          ></a-select>-->
              <a-dropdown v-model:visible="visibleSearchHistory" >
                <a-input
                    size="small"
                    v-model:value="searchWords"
                    placeholder="搜索"
                    style="width: 150px;margin-top: 8px"
                    @focus="onFocus"
                    spellcheck="false"
                    @change="onSearch"
                    @blur="addHistory"
                >
                  <template #prefix>
                    <a-dropdown v-model:visible="searchTypeVisible">
                      <template #overlay>
                        <a-menu @click="handleSearchTypeMenuClick">
                          <a-menu-item   key="name">
                            <a-checkbox v-model:checked="searchType.name"  >
                              标题
                            </a-checkbox>
                          </a-menu-item>
                          <a-menu-item  key="remark">
                            <a-checkbox  v-model:checked="searchType.remark" >
                              备注
                            </a-checkbox>
                          </a-menu-item>
                          <a-menu-item  key="href">
                            <a-checkbox   v-model:checked="searchType.href">
                              链接
                            </a-checkbox>
                          </a-menu-item>
                        </a-menu>
                      </template>
                      <span>
                  <search-outlined />
                  <DownOutlined />
                </span>
                    </a-dropdown>
                  </template>
                </a-input>
                <template #overlay>
                  <a-menu @click="setSearchWords">
                    <a-menu-item :disabled="true">
                      搜索历史
                    </a-menu-item>
                    <a-menu-item v-for="history in filteredSearchHistory"   :key="history">
                      {{ history }}
                    </a-menu-item>
                  </a-menu>
                </template>
              </a-dropdown>
        </a-col>
        <a-col>
          <div v-if="searchWords!==''" style="color: #999;font-size: 12px;margin-left: 20px">
            搜索：<strong style="color: #f81f1f">{{searchWords}}</strong>
            <br>
            位置：
            <a-checkable-tag v-model:checked="searchCurrent">"{{ folder.all.name===''?'收藏夹': folder.all.name }}"</a-checkable-tag>
            <a-checkable-tag v-show="folder.all.relativeFullPath!==''" v-model:checked="searchRoot" >"收藏夹"</a-checkable-tag>
            <a-checkbox v-show="!searchRoot" style="font-size: 12px;margin-left: 10px" v-model:checked="searchSubFolder">搜索子文件夹</a-checkbox>
          </div>
        </a-col>
      </a-row>
      <div id="refreshBtnWrapper" style="z-index:99;position: fixed;top:80px;text-align: center" :style="{right:Number(dividerPos.right+20)+'px'}">
        <a-button title="刷新" @click="refresh()" id="refreshingBtn"  :loading="refreshing" type="primary" shape="circle" style="text-align: center">
          <template #icon>
            <RedoOutlined  />
          </template>
        </a-button>
      </div>
    </div>
  </div>
</template>

<script>
import {RedoOutlined, LoginOutlined,DownOutlined ,SearchOutlined,AppstoreOutlined,UnorderedListOutlined,LeftOutlined} from '@ant-design/icons-vue';// BorderOuterOutlined,
import { mapState ,mapGetters} from 'vuex'
import ipc from '@/utils/ipc'
export default {
  name: 'ContentHeader',
  props: {
  },
  components:{
    //BorderOuterOutlined,
    RedoOutlined,
    LoginOutlined,DownOutlined,SearchOutlined,AppstoreOutlined,UnorderedListOutlined,LeftOutlined
  },
  data() {
    return {
      layoutUpdate:0,
      searchTypeVisible:false,
      searchCurrent:true,//搜索当前
      visibleSearchHistory:false,
      searchWords:'',
      searchHistory:[],
      filteredSearchHistory:[],
      searchType: {
        name:true,
        remark:true,
        href:true
      },
      refreshing:false,
      routes: [
        {
          path: 'index',
          breadcrumbName: 'First-level Menu',
        },
        {
          path: 'first',
          breadcrumbName: 'Second-level Menu',
        },
        {
          path: 'second',
          breadcrumbName: 'Third-level Menu',
        },
      ]
    }
  },

  mounted(){
    this.$store.commit('ui/setItemSize',Number(localStorage.getItem('itemSize')))
    let searchHistoryString=localStorage.getItem('searchHistory') || '[]'
    this.searchHistory=JSON.parse(searchHistoryString)
    this.filteredSearchHistory=this.searchHistory.slice(0,8)
  },

  methods:{
    handleSearchTypeMenuClick(){
    },
    setSearchWords(input){
      this.searchWords=input.key
      this.doSearch()
    },
    async doSearch(){
      this.$store.commit('ui/setSearcher',{
        words:this.searchWords,
        type:this.searchType,
        searchSubFolder: true,
        searchRoot:false
      })
    },
    onFocus(event){
      setTimeout(()=>{
        event.target.select()
      },200)
    },
    async onSearch(input){
      let value=input.target.value
      if(value===''){
        this.filteredSearchHistory=this.searchHistory.slice(0,8)
        this.doSearch()
        return
      }
      this.visibleSearchHistory=true
      this.filterSearchHistory(value)
      this.doSearch()
    },
    async filterSearchHistory(value){
      if(value===''){
        return this.filteredSearchHistory=this.searchHistory.slice(0,8)
      }
      this.filteredSearchHistory=this.searchHistory.filter(record=>{
        return record.toLowerCase().indexOf(value.toLowerCase())>-1
      })
      this.filteredSearchHistory= this.filteredSearchHistory.slice(0,8)
      if(this.filteredSearchHistory.length===0){
        this.visibleSearchHistory=false
      }
    },
    addHistory(input){
      let value=input.target.value
      if(value==='') return
      let exists=this.searchHistory.find((v)=>{
        return v===value
      })
      if(!exists)
      {
        this.searchHistory.push(value)
        localStorage.setItem('searchHistory',JSON.stringify(this.searchHistory))
      }
    },
    refresh(){
      this.refreshing=true
      this.$store.dispatch('ui/reload',()=>{
        this.refreshing=false
      })
      document.getElementById('refreshingBtn').classList.add('refreshing')
      setTimeout(()=>{
        document.getElementById('refreshingBtn').classList.remove('refreshing')
      },500)
    },
    openDir(dir){
      ipc.openDir(dir)
    },
    changeTab(){
      this.refreshing=true
      this.layoutUpdate=!this.layoutUpdate
      this.$store.dispatch('ui/changeTab',{name:'all'})
      setTimeout(()=>this.refreshing=false,500)
    }
  },
  watch:{
    searchCurrent(newValue){
      this.searchRoot=!newValue
    }
  },
  computed:{
    layoutType:{
      get(){
        return this.layout
      },
      set(newValue){
          this.$store.commit('ui/setLayout',newValue)
      }
    },
    itemSize: {
      get() {
        return this.$store.state.ui.itemSize
      },
      set(newValue) {
       this.$store.commit('ui/setItemSize',newValue)
        localStorage.setItem('itemSize',newValue)
      }
    },
    searchSubFolder:{
      get() {
        return this.$store.state.ui.searcher.searchSubFolder
      },
      set(newValue) {
        this.$store.commit('ui/setSearcher',Object.assign(this.$store.state.ui.searcher,{searchSubFolder:newValue}))
      }
    },
    searchRoot:{
      get(){
        return this.$store.state.ui.searcher.searchRoot
      },
      set(newValue){
        this.$store.commit('ui/setSearcher',Object.assign(this.$store.state.ui.searcher,{searchRoot:newValue}))
        this.searchCurrent=!newValue
      }

    },
    ...mapGetters({
        layout:'ui/layout'
        }
     ),
    ...mapState({

      isApp:state=>state.config.isApp,
      currentFolder:state=>state.ui.currentFolder,
      currentTab:state=>state.ui.currentTab,
      openHistory:state=>state.ui.openHistory,
      dividerPos:state=>state.ui.dividerPos,
      folder:state=>state.ui.currentFolder,

    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@keyframes rotate{
  0%{
    transform: rotate(0);
  }
  100%{
    transform:rotate(365deg);
  }
}
.refreshing{
  transition: 0.2s;
  animation: rotate .5s linear infinite;  /*开始动画后无限循环，用来控制rotate*/
}
</style>
