import configDb from './db/configDb'
import contentTypeHelper from '@/utils/contentTypeHelper'
const path= eval('require')('path')
const pathTools = {
  join(a,b){
    return  path.join(a,b).replaceAll('\\','/').replaceAll('//','/')
  },
  /**
   * 自动处理双斜杠
   * @param path
   */
  convertToCommonFilePath (path, protocol = true) {
    if (!path.startsWith('file://') && protocol) {
      path = 'file://' + path
    }
    path = pathTools.replaceSlash(path)
    return path
  },
  /**
   * 自动处理双斜杠，自动添加上存储路径
   * @param dir
   */
  convertToCommonDir (dir) {
    dir = pathTools.replaceSlash((window.$defaultStrorePath + dir))
    if (!dir.endsWith('/')) {
      dir = dir + '/'
    }
    return dir
  },
  /**
   * 将一个路径转为通用的相对路径，替换掉右斜杠为左斜杠，去除重复左斜杠，自动补充相对根路径的左斜杠
   * @param path dir1/dir2//dir3\\di4
   * @returns {string}  /dir1/dir2/dir3/dir4
   */
  convertToCommonRelativePath (path) {
    path = pathTools.replaceSlash(path)
    if (!path.startsWith('/')) {
      path = '/' + path
    }
    path.replaceAll('//', '/')
    return path
  },
  /**
   * 会自动整理/，在头部加上/，在尾部加上/
   * @param dir
   */
  convertToCommonRelativeDir (dir) {
    dir = pathTools.convertToCommonRelativePath(dir)
    if (!dir.endsWith('/')) {
      dir = dir + '/'
    }
    return dir
  },
  /**
   * 获取没有/结尾的通用目录路径
   * @param dir
   * @returns {string}
   */
  convertToCommonDirNoEndsSplash(dir){
    dir=pathTools.convertToCommonRelativeDir(dir)
    if(dir.endsWith('/')){
      return dir.substr(0,dir.length-1)
    }
    return dir
  },

  replaceSlash (str) {
    return str.replaceAll('\\', '/').replaceAll('//', '/')
  },
  getFolderFromRelativeFullPath (relativeFullPath) {
    return relativeFullPath.substr(0, relativeFullPath.lastIndexOf('/'))
  },
  getFilenameFromRelativeFullPath (relativeFullPath) {
    return relativeFullPath.substr(relativeFullPath.lastIndexOf('/') + 1)
  },
  getNameFromRelativeFullPath (relativeFullPath) {
    let filename = relativeFullPath.substr(relativeFullPath.lastIndexOf('/'))
    return filename.substr(0, filename.lastIndexOf('.')).replaceAll('/', '')
  },

  getRelativeFullPathFromFullPath (fullPath) {
    let replacement=pathTools.convertToCommonFilePath(window.$defaultStrorePath,false)
    let fullPathCommon=pathTools.convertToCommonFilePath(fullPath,false)
    return pathTools.convertToCommonRelativePath(fullPathCommon.replace(replacement, ''))
  },
  getFullPathFromRelativeFullPath (relativeFullPath) {
    return (window.$defaultStrorePath + relativeFullPath).replaceAll('//', '/')
  },
  removeLastSlash (dir) {
    if (dir.endsWith('/')) {
      dir = dir.substr(0, dir.length - 1)
    }
    return dir
  },
  /**
   * 过滤文件名，并替换掉违规字符
   * @param filename 文件名
   * @param replaceHolder 替换字符，默认为空格
   * @returns {{hadMapped: *[], filename}} 返回包含了文件名和匹配到的内容的对象
   */
  filterFilename (filename, replaceHolder = ' ') {
    const map = [
      '/', '\\', '|', '?', '"', '*', ':', '<', '>', '\n'
    ]
    let hadMapped = []
    map.forEach(replace => {
      if (filename.indexOf(replace) > -1) {
        hadMapped.push(replace)
      }
      filename = filename.replaceAll(replace, replaceHolder)
    })
    return {
      filename,
      hadMapped
    }
  },
  /**
   * 从相对路径获取真实的存储路径，不区分目录与文件
   * @param relativePath
   * @param protocol
   * @returns {*}
   */
  getStorePathFromRelativePath (relativePath, protocol = false) {
    return pathTools.convertToCommonFilePath(configDb.getStorePath() + '/' + relativePath, protocol)
  },

  /**
   * 通过相对完整的路径获取全部的路径以便使用，防止搞混，建议使用此方法来获取要用到的各个参数
   * @param relativeFullPath  /文件夹1/文件名.jpg
   * @returns {{storeFullPathProtocol, storeFullPath, relativeFolder, name, filename}}
   */
  getAllFromRelativeFullPath (relativeFullPath) {
    let name = pathTools.getNameFromRelativeFullPath(relativeFullPath)//xxx
    let filename = pathTools.getFilenameFromRelativeFullPath(relativeFullPath) //xxx.jpg
    let relativeFolder = pathTools.convertToCommonRelativePath(pathTools.getFolderFromRelativeFullPath(relativeFullPath))//  /文件夹1/
    if(!relativeFolder.endsWith('/')) relativeFolder=relativeFolder+'/'
    let storeFullPath = pathTools.getStorePathFromRelativePath(relativeFullPath, false) //   d://收藏夹.lab/fold/xxx.jpg
    let storeFullPathProtocol = pathTools.getStorePathFromRelativePath(relativeFullPath, true) // file://d://收藏夹.lab/文件夹1/文件名.jpg
    let storeFolder=storeFullPath.substr(0, storeFullPath.lastIndexOf('/'))
    let storeFolderProtocol='file:///'+storeFullPathProtocol

    let extNoDot = contentTypeHelper.getExtNoDotFromFilename(filename)
    let ext = '.' + extNoDot
    let all = {
      name,
      filename,
      relativeFolder,
      storeFullPath,
      storeFullPathProtocol,
      storeFolder,
      storeFolderProtocol,
      relativeFullPath,
      extNoDot,
      ext
    }
    return all
  },
  /**
   * 通过相对完整的路径获取全部的路径以便使用，防止搞混，建议使用此方法来获取要用到的各个参数
   * @param relativeFullPath  /父文件夹/文件夹
   * @returns {{storeFullPathProtocol, storeFullPath, relativeFolder, name, filename}}
   */
  getFolderAllFromRelativeFullPath (relativeFullPath) {
    let commonRelativeFullPath=pathTools.convertToCommonDirNoEndsSplash(relativeFullPath)
    relativeFullPath=commonRelativeFullPath
    let name =commonRelativeFullPath
    name = name.substr(name.lastIndexOf('/')+1,name.length)
    let parentFolderRelativeFullPath = relativeFullPath.substr(0,relativeFullPath.lastIndexOf('/'))//  /父文件夹/
    let storeFullPath = pathTools.getStorePathFromRelativePath(relativeFullPath, false) //   d://收藏夹.lab/fold/xxx
    let parentFolderStoreFullPath=pathTools.getStorePathFromRelativePath(parentFolderRelativeFullPath,false)
    let storeFullPathProtocol = pathTools.getStorePathFromRelativePath(relativeFullPath, true) // file://d://收藏夹.lab/文件夹1/文件名
    let parentFolderStoreFullPathProtocol=pathTools.getStorePathFromRelativePath(parentFolderRelativeFullPath,true)
    let all = {
      name,
      parentFolderRelativeFullPath,
      storeFullPath,
      parentFolderStoreFullPath,
      storeFullPathProtocol,
      parentFolderStoreFullPathProtocol,
      relativeFullPath
    }
    return all
  },
  appendEndsSplash(dir){
    if(!dir.endsWith('/')){
      dir=dir+'/'
    }
    return dir
  }
}

export default pathTools
