const contentTypeMap = [
    {
        ext: 'jpg,jpeg,png,bmp,svg,webp,gif',
        alias: '图片',
        name: 'img'
    },
    {
        ext: 'rp',
        alias: '原型',
        name: 'prototype'
    }, {
        ext: 'js,php',
        alias: '代码',
        name: 'code'
    },
    {
        ext: 'doc,docx,ppt,pptx,xls,xlsx,pdf',
        alias: '文档',
        name: 'doc'
    },
    {
        ext: 'mp4,avi,rmvb,mpg,m4v,mov',
        alias: '视频',
        name: 'video'
    },
    {
        ext:'wav,mp3',
        alias:'音频',
        name:'audio'
    },
    {
        ext:'url',
        alias:'网页',
        name:'url'
    },
    {
        ext:'',
        alias:'文件夹',
        name:'folder'
    }
]

const canOpenInside = [
    'img','video','url'
]

const contentTypeHelper = {
    /**
     * 内容类型配对表
     * [
     {
        ext: 'jpg,jpeg,png,bmp',
        alias: '图片',
        name: 'img'
    }]
     */
    contentTypeMap,
    /**
     * 可以被内置处理的类型表
     * ['img']
     */
    canOpenInside,
    /**
     * 根据扩展名判断该类型是否能被内部处理
     * @param ext .jpg jpg
     */
    extCanOpenInside(ext) {
        const typeName = contentTypeHelper.getContentTypeNameFromExt(ext)
        return canOpenInside.indexOf(typeName) > -1
    },
    /**
     * 从文件名中获取不包含.的扩展名，
     * @param filename  filename.ext
     * @returns {string|string|string|*|string}  ext
     */
    getExtNoDotFromFilename(filename) {
        if (filename.lastIndexOf('.') > -1) {
            return filename.substr(filename.lastIndexOf('.')+1)
        } else
            return ''
    },

    /**
     * 从扩展名获得一个内容类型的对象，扩展名可包含.也可不包含.如果包含.，会自动去除
     * @param ext .jpg or jpg
     */
    getContentTypeFromExt(ext) {
        let extNoDot = ext.replaceAll('.', '')
        const typeMatched = contentTypeMap.filter((type) => {
            const typeExts = type.ext.split(',')
            if (typeExts.indexOf(extNoDot) > -1) return true
        })
        if (typeMatched.length > 0) {
            return typeMatched[0]
        } else {
            return {
                ext: '.other',
                alias: '其他',
                name: 'other'
            }
        }
    },
    /**
     * 通过扩展名获得内容类型的中文别名，ext可带.不带.，返回内容形如 原型、图片、代码
     * @param ext .jpg or jpg
     * @returns {string|*} 原型、图片、代码
     */
    getContentTypeAliasFromExt(ext) {
        return contentTypeHelper.getContentTypeFromExt(ext).alias
    },
    /**
     * 通过扩展获得内容类型名称，扩展可以带.不带.，扩展名形如img,code,prototype
     * @param ext
     * @returns {string|*}
     */
    getContentTypeNameFromExt(ext) {
        return contentTypeHelper.getContentTypeFromExt(ext).name
    },
    getContentTypeFromFilename(filename){
      return contentTypeHelper.getContentTypeFromExt(contentTypeHelper.getExtNoDotFromFilename(filename))
    },
    /**
     * 通过内容类型名称获得内容类型定义，例如img
     * @param name
     * @returns {{ext: string, name: string, alias: string}|unknown}
     */
    getContentTypeDefine(name) {
        let matched = contentTypeMap.map(type => {
            if (type.name === name)
                return type
        })
        if (matched.length > 0) {
            return matched[0]
        } else {
            return {
                ext: '',
                alias: '其他',
                name: 'other'
            }
        }
    },
    getContentCover(ext){
        let icon = 'other/' + contentTypeHelper.getContentTypeFromExt(ext).name
        ext=ext.toLowerCase()
        switch (ext) {
            case '.pdf':
                icon = 'pdf'
                break
            case '.ppt':
            case '.pptx':
                icon = 'ppt'
                break
            case '.doc':
            case '.docx':
                icon = 'word'
                break
            case '.xls':
            case '.xlsx':
                icon = 'excel'
                break
            case '.zip':
            case '.7z':
            case '.rar':
            case '.tar.gz':
                icon = 'zip'
        }
        return './img/type/' + icon + '.svg'
    }
}

export default contentTypeHelper
